import axios from 'axios'; //   axios   HTTP-
import { createAsyncThunk } from '@reduxjs/toolkit'; //   createAsyncThunk  Redux Toolkit

axios.defaults.baseURL = 'https://connections-api.herokuapp.com/'; //   URL  HTTP-

//   thunk   
export const fetchContacts = createAsyncThunk(
  'contacts/fetchContacts', //   (action)
  async (_, { rejectWithValue }) => {
    try {
      const response = await axios.get('/contacts'); //  GET-  /contacts
      return response.data; //   
    } catch (e) {
      return rejectWithValue(e.message); //        
    }
  }
);

//   thunk   
export const deleteContact = createAsyncThunk(
  'contacts/deleteContact', //   (action)
  async (id, { rejectWithValue }) => {
    try {
      const response = await axios.delete(`/contacts/${id}`); //  DELETE-  /contacts/{id}
      return response.data; //   
    } catch (e) {
      return rejectWithValue(e.message); //        
    }
  }
);

//   thunk   
export const addContact = createAsyncThunk(
  'contacts/addContact', //   (action)
  async (subscriber, { rejectWithValue }) => {
    try {
      const response = await axios.post(`/contacts`, subscriber); //  POST-  /contacts   ' 
      return response.data; //   
    } catch (e) {
      return rejectWithValue(e.message); //        
    }
  }
);

//   thunk   
export const redactContatc = createAsyncThunk(
  'contacts/redactContatc', //   (action)
  async (data, { rejectWithValue }) => {
    try {
      const response = await axios.patch(`/contacts/${data.id}`, {
        //  PATCH-  /contacts/{id}     
        name: data.name,
        number: data.number,
      });
      return response.data; //   
    } catch (e) {
      return rejectWithValue(e.message); //        
    }
  }
);

